@import "src";

.container {
  display: flex;
  justify-content: space-between;
  gap: 10px;
  padding: 15px 0;

  label {
    display: flex;
    gap: 25px;
  }

  input[type='text'] {
    padding: 8px 12px;
    height: 35px;
    width: 80%;
    border: 1px solid #D1D1D1;
    border-radius: 5px;
    outline: none;
  }
}

.form__about, .form__socials {
  width: 50%;
  padding: 15px;
  background-color: #ffffff;
  box-shadow: 0 .5rem 1rem rgba(0, 0, 0, .03);
  border-radius: 10px;

  display: flex;
  flex-direction: column;
  gap: 15px;

  label {
    display: flex;
    justify-content: space-between;
  }

  .inputCheckbox {
    justify-content: flex-start;

    input[type=checkbox] {
      position: relative;
      cursor: pointer;
    }
    input[type=checkbox]:before {
      content: "";
      display: block;
      position: absolute;
      width: 15px;
      height: 15px;
      top: 0;
      left: 0;
      background-color:#e9e9e9;
    }
    input[type=checkbox]:checked:before {
      content: "";
      display: block;
      position: absolute;
      width: 15px;
      height: 15px;
      top: 0;
      left: 0;
      background-color: $mainColor;
    }
    input[type=checkbox]:checked:after {
      content: "";
      display: block;
      width: 3px;
      height: 6px;
      border: solid white;
      border-width: 0 2px 2px 0;
      -ms-transform: rotate(45deg);
      transform: rotate(45deg);
      position: absolute;
      top: 2px;
      left: 5px;
    }
  }
}


.form__inputFile {

  input {
    visibility: hidden;
  }

  span {
    margin-right: 36px;
    color: #888;
    background-color: #f5f5f5;
    padding: 5px;
    text-align: center;
    cursor: pointer;
    border-radius: 10px;


    &:hover {
      color: $mainColor;
      background-color: #fff0da;
    }
  }
}

.buttonsGroup {
  display: flex;
  justify-content: flex-end;
  gap: 10px;

  button {
    height: 35px;
    width: 100px;
    border: none;
    color: #888;
    background-color: #f5f5f5;
    padding: 5px;
    text-align: center;
    cursor: pointer;
    border-radius: 10px;

    &:hover {
      color: $mainColor;
      background-color: #fff0da;
    }

    &:first-of-type {
      color: $mainColor;
      background-color: #fff0da;

      &:hover {
        color: #888;
        background-color: #f5f5f5;
      }
    }
  }
}